import { useEffect, useState } from "react";
/* import React from 'react'; */
import Header from '../header/header';
import Footer from '../footer/footer';
import AdminHeader from '../../pages/admin/header/header'
import AdminSidebar from '../../pages/admin/sidebar/sidebar'
import Login from '../../pages/admin/login';
import { NotificationManager } from 'react-notifications';
import "./layout.scss";
import { useParams } from "react-router-dom";

import axios from "axios";
// axios.defaults.baseURL = "http://127.0.0.1"
const Layout = (props) => {
    const { id } = useParams();
    const [sidebarFlag, setSidebarFlag] = useState(false);
    const [login, setLogin] = useState(false);
    useEffect(() => {
        console.log('id : ')
        console.log(id)
    }, [])

    useEffect(() => {
        const SetResponsiveness = () => {
            return window.innerWidth < 990
                ? setSidebarFlag(true)
                : setSidebarFlag(false);
        };
        SetResponsiveness();
        window.addEventListener("resize", () => SetResponsiveness());
    }, []);

    const ToggleSidebar = async () => {
        setSidebarFlag(!sidebarFlag);
    };
    const checkLogin = async()=>{
        // try {
        //     axios.post('/checkLogin', {}).then((e) => {
		// 		if (e.data == 'success') {
		// 			NotificationManager.success("Success!")
        //             setLogin(true)
		// 		} else if (e.data = 'fail') {
		// 			NotificationManager.error("Invalid Username or Password")
		// 		}
		// 	})
        // } catch (error) {
        //     NotificationManager.error(error.message)
        //     setLogin(false)
        // }
    }
    useEffect(()=>{
        checkLogin()
    })
    return (<>
        {
            id === 'admin' ?
                <>
                    {
                        login ?
                            <div className={id === 'admin' && !sidebarFlag ? "content" : "content open"}>
                                <AdminHeader setLogin={setLogin} ToggleSidebar={ToggleSidebar} />
                                <main className={'admin-container'} >
                                    <div className={'container'}>
                                        <AdminSidebar  sidebarFlag={sidebarFlag} />
                                        {props.children}
                                    </div>
                                </main>
                            </div>
                            :
                            <Login setLogin={setLogin} />

                    }
                </> : <>

                    <div className={id === 'admin' && !sidebarFlag ? "content" : "content open"}>
                        <Header />
                        <main className={''} >
                            <div className={''}>
                                {props.children}
                            </div>
                        </main>
                        <Footer />
                    </div>
                </>
        }
    </>);
}

export default Layout;