
import React, { useEffect, useState } from 'react';
import { NotificationManager } from 'react-notifications';
import { useNavigate, Link } from 'react-router-dom';
import './admin.scss';
import axios from "axios";
// axios.defaults.baseURL = "http://127.0.0.1"
const Login = ({ setLogin }) => {

    const [loading, setLoading] = useState(false);

    const [walletModal, setWalletModal] = useState(false);
    const [isMobile, setIsMobile] = useState(false);
    const navigate = useNavigate();

    const [userName,setUserName] = useState('')
    const [userPass,setUserPass] = useState('')
    const signIn = async()=>{
        if(userName==''||userPass==''){
            NotificationManager.error("Please input username or password")
        }
        const data = {
			userName: userName,
			userPass: userPass,
		}
        try {
            axios.post('/login', data).then((e) => {
				if (e.data == 'success') {
					NotificationManager.success("Success!")
                    setLogin(true)
				} else if (e.data = 'fail') {
					NotificationManager.error("Invalid Username or Password")
				}
			})
        } catch (error) {
            NotificationManager.error(error.message)
            setLogin(false)
        }
    }
    return (
        <div className="login-container">
            <div className="login-body">
                <h3 className=''>
                    Sign In
                </h3>
                <div className="justify fd-c mt-8">
                    <input type="text" className="" placeholder='User Name' defaultValue={userName} onChange ={(e)=>{setUserName(e.target.value)}} />
                    <input type="password" className="" placeholder='Password' defaultValue={userPass} onChange ={(e)=>{setUserPass(e.target.value)}}/>
                </div>
                <div className="mt-8 dis-f jc-e">
                    <button onClick={signIn} className="btn signin-btn">Sign In</button>
                </div>
            </div>
        </div>
    );
};

export default Login;
